import React from "react";
import { ZtreeInput, ZerodMainContext } from "zerod";
import { Row, Col } from "antd";
import HOC from "@/HOC/load-HOC.js";
const { AmdDocHOC } = HOC;
import doc from "zerod/components/ZtreeInput/doc.md";
export default AmdDocHOC(doc, {
	demo1: function(tool) {
		// console.log(tool);
		class Myjavascript extends React.PureComponent {
			state = {
				tree: [],
			};
			treeChange = (value) => {
				this.setState({
					tree: value,
				});
			};
			render() {
				return <div style={{width:"500px"}}><ZtreeInput value={this.state.tree} onChange={this.treeChange} /></div>;
			}
		}
		return <Myjavascript />;
	},
	demo2: function(tool) {
		// console.log(tool);
		class Myjavascript extends React.PureComponent {
			state = {
				tree: [],
			};
			treeChange = (value) => {
				this.setState({
					tree: value,
				});
			};
			render() {
				return <div style={{width:"500px"}}><ZtreeInput inputType="single" value={this.state.tree} onChange={this.treeChange} /></div>;
			}
		}
		return <Myjavascript />;
	},
	demo3: function(tool) {
		// console.log(tool);
		class Myjavascript extends React.PureComponent {
			state = {
				tree: [],
			};
			treeChange = (value) => {
				this.setState({
					tree: value,
				});
			};
			render() {
				return <div style={{width:"500px"}}><ZtreeInput multiple={false} value={this.state.tree} onChange={this.treeChange} /></div>;
			}
		}
		return <Myjavascript />;
	},
});
